<template>
  <div id="home">
    <div v-if="mobile">
      <div class="text-centered" v-for="project in projects">
        <h5><router-link :to="project.slug">{{project.name}}
                </router-link></h5>
      </div>
    </div>
    <div class="dview" :style="{ background: background }" @mousemove="follow" @click="goTo(project)" v-else>
      <div class="preview" v-show="isLoad">
        <img :src="preview" @load="loaded">
      </div>
        
      <div class="follow" :style="{ left: pageX + 'px', top: pageY + 'px' }">
        <p :style="{ marginLeft: offsetLeft + 'px' }" ref="project" >{{project.name}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      backgrounds: [
        '#cc55cc',
        '#55cccc',
        '#cccc55',
        '#5555cc',
        '#cc5555',
        '#ff99ff',
        '#99ffff',
        '#ff9999',
        '#99ff99',
        '#9999ff',
        '#55cc55',
        '#ffff99',
      ],
      background: '#fff',
      projects: [
        {'name': 'Spit', 'slug': 'face', 'imgs': ['face.png']},
        {'name': 'Smiley', 'slug': 'smiley', 'imgs': ['smiley.png']},
        {'name': 'Formos', 'slug': 'formos', 'imgs': ['formos.png']},
        {'name': 'Blank race', 'slug': 'blank', 'imgs': ['blank.png']},
        {'name': 'oo00o0o0oo00', 'slug': 'ooo', 'imgs': ['oo00o0o0oo00.png']},
        {'name': 'Shaders', 'slug': 'shaders', 'imgs': ['shaders.png']},
        {'name': 'CV', 'slug': 'luis', 'imgs': ['luis.png']},
        {'name': 'Contact', 'slug': 'contact', 'imgs': ['contact.png']},
        {'name': 'Wait', 'slug': 'wait', 'imgs': ['wait.png']},

        // 'Swipe',
      ],
      project: {},
      bkginterval: null,
      loop: 0,
      coords: '',
      pageX: -100,
      pageY: -100,
      offsetLeft: 0,
      preview: null,
      mobile: false,
      isLoad: false,
    }
  },
  created () {
    if (window.innerWidth < 700) {
      this.mobile = true
    } else {
      this.mobile = false
    }
  },
  methods: {
    selectProject () {
      this.bkginterval = setInterval(this.changeBkg, 200)
    },
    changeBkg () {
      var bg = parseInt(Math.random() * 3)
      // console.log(bg)
      this.project = this.projects[bg]
      this.background = this.backgrounds[bg]
    },
    stopInterval () {
      clearInterval(this.bkginterval)
    },
    follow (event) {
      this.pageX = event.pageX
      this.pageY = event.pageY
      this.coords = event.pageX + ' ' + event.pageY
      var prop = window.innerWidth / this.projects.length
      // var coord = parseInt(event.pageY/prop) * parseInt(window.innerWidth/prop) + parseInt(event.pageX/prop)
      var coord = parseInt(event.pageX/prop)
      this.project =  this.projects[coord]
      if (coord != this.coord) {
        this.isLoad = false
      }
      this.coord = coord
      this.preview =  this.screenUrl(this.projects[coord].imgs[0])
      // this.background = this.backgrounds[coord]

      let objX = this.$refs.project.offsetWidth + event.pageX
      if (objX >= window.innerWidth) {
        this.offsetLeft = window.innerWidth - objX - 10
      } else {
        this.offsetLeft = 0
      }
    },
    goTo (project) {
      this.$router.push(project.slug)
    },
    loaded () {
      this.isLoad = true
      console.log('loaded', this.isLoad)
    },
    screenUrl (img) {
      return require(`@/assets/screens/${img}`)
    }
  },
  computed: {
  }
}
</script>

<style>
.dview {
  height: 100%;
  font-family: serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  cursor: pointer;
  position: relative;
}
.follow {
  /*background: #000;*/
  font-size: 4rem;
  position: absolute;
  top: 0;
}
.follow p {
  background: #fcfcfc;
  padding: 0 10px;
}
.preview {
  width: 100%;
  max-width: 600px;
  margin: auto;
  padding-top: 100px;
  position: relative;
}
.preview img {
  width: 100%;
}
a:focus {
  outline: none;
}
</style>
