Vue.use(Router)

const router = new Router({
    routes : [
        { path: '/', component: Home },
        { path: '/face', component: Face },
        { path: '/smiley', component: Smiley },
        { path: '/formos', component: Formos },
        { path: '/blank', component: Blank },
        { path: '/ooo', component: Ooo },
        { path: '/shaders', component: Shaders },
        { path: '/wait', component: Wait },
        { path: '/luis', component: CV },
        { path: '/contact', component: Contact },
    ]
})

new Vue({
  render: h => h(App),
  router
}).$mount("#app")


