<template>
  <div id="wait">
    {{msg}}
  </div>
</template>

<script>
export default {
  name: 'wait',
  data () {
    return {
      msg: 'wait',
      loop: 0
    }
  },
  created () {
    setInterval(this.justWait, 1500)
  },
  methods: {
    justWait () {
      switch (this.loop) {
        case 0:
          this.msg += '...'
          break
        case 1:
          this.msg += ' just'
          break
        case 2:
          this.msg += '... '
          break
        case 3:
          this.msg += ' wait'
          this.loop = -1
          break
      }
      this.loop++
    }
  }
}
</script>

<style>
  #wait {
    padding-top: 100px;
  }
</style>
