<template>
  <div id="app">
    <div class="menu">
      <router-link to="/">[···]</router-link>
      <small>
        <router-link class="rdm" :to="{ path: `/${rdmProj}` }" v-if="rdmProj">somewhere random</router-link>
      </small>

    </div>
    <router-view class="content"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      projects: [
        {'name': 'Spit', 'slug': 'face', 'imgs': ['face.png']},
        {'name': 'Smiley', 'slug': 'smiley', 'imgs': ['smiley.png']},
        {'name': 'Formos', 'slug': 'formos', 'imgs': ['formos.png']},
        {'name': 'Blank race', 'slug': 'blank', 'imgs': ['blank.png']},
        {'name': 'oo00o0o0oo00', 'slug': 'ooo', 'imgs': ['oo00o0o0oo00.png']},
        {'name': 'Shaders', 'slug': 'shaders', 'imgs': ['shaders.png']},
        {'name': 'CV', 'slug': 'luis', 'imgs': ['luis.png']},
        {'name': 'Contact', 'slug': 'contact', 'imgs': ['contact.png']},
        {'name': 'Wait', 'slug': 'wait', 'imgs': ['wait.png']},
      ],
      rdmProj: null
    }
  },
  created () {
    // console.log(this.$router)
    this.$router.beforeEach((to, from, next) => {
        if (to.path != '/') {
          let rdm = parseInt(Math.random() * (this.projects.length - 1))
          console.log(rdm, this.projects.length)
          if (this.projects[rdm].slug == to.path.replace('/', '')) {
            if(rdm < this.projects.length - 1) {
              rdm += 1
            } else {
              rdm -= 1
            }
          }
          this.rdmProj = this.projects[rdm].slug
          console.log(this.rdmProj)
        } else {
          this.rdmProj = null
        }
        next()
    })
  },
  methods: {
  }
}
</script>

